import Vue from 'vue';
import VueRouter, { RouteConfig } from 'vue-router';
import HomePage from '../views/HomePage.vue';
import AboutPage from '../views/AboutPage.vue';
import StagePage from '../views/StagePage.vue';
import RealisatiePage from '../views/RealisatiePage.vue';
import UnityPage from '../views/UnityPage.vue';
import ProjectPage from '../views/ProjectPage.vue';
import WpfPage from '../views/WpfPage.vue';
import ErasmusPage from '../views/ErasmusPage.vue';
Vue.use(VueRouter);

export const routes: Array<RouteConfig> = [
	{
		path: '/',
		name: 'Welkom',
		component: HomePage
	},
	{
		path: '/about',
		name: 'Over mij',
		component: AboutPage
	},
	{
		path: '/stage',
		name: 'Stage',
		component: StagePage
	},
    {
        path: '/realisatie',
        name: 'Realisaties',
        component: RealisatiePage
    },
    {
        path: '/erasmus',
        name: 'Erasmus',
        component: ErasmusPage
    },
    {
        path: '/unity',
        name: 'Unity',
        component: UnityPage
    },
    {
        path: '/project',
        name: 'Project',
        component: ProjectPage
    },
    {
        path: '/wpf',
        name: 'Wpf',
        component: WpfPage
    },
];

const router = new VueRouter({
	routes
});

export default router;
